<template>
    <div class="theme" @click="$emit('click')">
        <img :src="theme.picture"/>
        <p>{{ theme.name }}</p>
    </div>
</template>

<script>
export default {
    props: [ 'theme' ]
}
</script>

<style lang="scss" scoped>
.theme {
    border: 1px solid black;
    display: inline-block;
    padding: 10px;

    &:hover {
        background-color: #eee;
        cursor: pointer;
    }

    &.selected {
        background-color: #a2a2f0;
    }

    img {
        width: 100px;
        height: 100px;
    }
}
</style>