import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import GamesMenu from './components/GamesMenu.vue'
import PGame from './components/PGame.vue'
import Words from './components/Words.vue'

Vue.use(VueRouter);

const routes = [
  { path: '/', component: GamesMenu },
  { path: '/pgame', name: 'pgame', component: PGame, props: true },
  { path: '/words', name: 'words', component: Words, props: true }
]

const router = new VueRouter({
  routes // short for `routes: routes`
})

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
