<template>
    <div>
        <h1>Menu</h1>
        <h2>Theme</h2>
        <ThemeBloc v-for="theme in themes" :key="theme.name" :theme="theme" @click="chooseTheme(theme)" :class="selected(theme) ? 'selected' : ''"/>
        <div class="actions" v-if="chosenTheme !== ''">
            <button @click="openWords">Vocabulary</button>
            <button @click="openPictureGame">Picture Game</button>
        </div>
    </div>
</template>

<script>
import ThemeBloc from './ThemeBloc.vue';

export default {
    components: {
        ThemeBloc,
    },
    data() {
        return {
            themes: [
                {
                    name: 'Ice cream',
                    picture: 'img/icecream1.jpg',
                    items: [
                        { name: 'Chocolate Cone', img: 'img/icecream1.jpg', attributes: [ 'chocolate', 'cone', '1 scoop' ] },
                        { name: 'Chocolate Waffle', img: 'img/icecream2.jpg', attributes: [ 'chocolate', 'waffle', '1 scoop' ] },
                        { name: 'Caramel in a Cup', img: 'img/icecream3.jpg', attributes: [ 'caramel', 'cup,', '1 scoop' ] },
                        { name: 'Rainbow in a Cup', img: 'img/icecream4.jpg', attributes: [ 'rainbow', 'cup', '1 scoop' ] },
                        { name: 'Chocolate cup', img: 'img/icecream5.jpg', attributes: [ 'rainbow', 'cup', '1 scoop' ] },
                        { name: 'Vanilla Cone', img: 'img/icecream6.jpg', attributes: [ 'rainbow', 'cup', '1 scoop' ] },
                        { name: 'Vanilla in a Cup', img: 'img/icecream7.jpg', attributes: [ 'rainbow', 'cup', '1 scoop' ] },
                        { name: 'Strawberry Cone', img: 'img/icecream8.jpg', attributes: [ 'rainbow', 'cup', '1 scoop' ] }
                    ]
                },
                {
                    name: 'Ella and Olivia - Words 1',
                    picture: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSbpUm2LKxM35j1mcEvpO5R4wwJYLGsdzeXbZ8HaEtOYb0LsFnX',
                    items: [
                        { name: 'Cockatiel', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQlp1P_O7lHEc_9J9GxFFK0YWNEB3hQip2U9N3iutdFESiiYv95'},
                        { name: 'Dolphin', img: 'data:image/jpeg;base64,'},
                        { name: 'Coin toss', img: 'data:image/jpeg;base64,'},
                        { name: 'Batter', img: 'data:image/jpeg;base64,'},
                        { name: 'Pitch', img: 'data:image/jpeg;base64,'},
                        { name: 'Jacaranda', img: 'data:image/jpeg;base64,'},
                        { name: 'Wicket', img: 'data:image/jpeg;base64,'},
                        { name: 'Boundary line', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSimxUwwlSlSxnQkCGE3hyuTDoVIlzD3CT5Fi6cmAAbwe_QVGoB'},
                        { name: 'Sore', img: 'data:image/jpeg;base64,'},
                        { name: 'Wanderer', img: 'data:image/jpeg;base64,'},
                        { name: 'Sun visor', img: 'data:image/jpeg;base64,'}
                    ]
                }
            ],
            chosenTheme: ''
        }
    },
    methods: {
        openPictureGame() {
            this.$router.push({name: 'pgame', params: {theme:this.chosenTheme}});
        },
        openWords() {
            this.$router.push({name: 'words', params: {theme:this.chosenTheme }});
        },
        chooseTheme(theme) {
            window.console.log("clicked");
            if (this.chosenTheme === '') {
                this.chosenTheme = theme;
            } else {
                this.chosenTheme = '';
            }
        },
        selected(theme) {
            return this.chosenTheme.name === theme.name;
        }
    }
}
</script>